:root {
  --bg-pc-shop-promo: linear-gradient(180deg, #060426 0%, #0E0832 47.28%, #0A072E 100%);
  --bg-mobile-shop-promo: linear-gradient(180deg, #060426 40.4%, #0A072E 76.96%, #0A072E 100%);
}

* {
  box-sizing: border-box;
  min-height: 0.001vw;
}

html.fixed {
  overflow: hidden;
  touch-action: none;
}

body {
  display: flex;
  flex-direction: column;
  background: #FFFFFF;
  font-family: MTSSans, Arial, sans-serif;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body.fixed {
  position: fixed !important;
  min-height: 0;
  overflow: hidden;
  touch-action: none;
  left: 0;
  width: 100%;
  height: fit-content;
}

.container {
  width: 100%;
  max-width: 850px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 40px;
}

.container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
  position: static;
  width: 100%;
  max-width: 72.8125rem;
  margin: 0 auto;
  padding: 0 15px;
}
@media (max-width: 75em) {
  .container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
    padding: 0 1.25rem;
  }
}

.layout {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  overflow: hidden;
  position: relative;
}
.layout .container-fluid, .layout .container-sm, .layout .container-md, .layout .container-lg, .layout .container-xl, .layout .container-xxl {
  max-width: 95.8125rem;
}

ul.row {
  list-style-type: none;
}
ul.row > li {
  display: block;
}

h1, .h1 {
  color: #303138;
  font-weight: 900;
  margin: 0;
  font-size: 1.5625rem;
  line-height: 1.2;
}
@media (min-width: 375px) {
  h1, .h1 {
    font-size: 1.75rem;
    line-height: 1.2142857143;
  }
}
